@import url(https://fonts.googleapis.com/css?family=Overpass+Mono);
@import url(https://fonts.googleapis.com/css?family=Poppins);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


:root {
  --card-text-space-left: 30px;
  --card-text-color-default: #ffffff;
  --card-text-font: "Overpass Mono", monospaced;
  --card-text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.7);
  --card-background-default: linear-gradient(90deg, #353535, #717171);
}

.wrapper {
  perspective: 1000px;
}

.card {
  width: 335px;
  height: 222px;
  min-width: 335px;
  border-radius: 10px;
 
  transform-style: preserve-3d;
  transition: all 0.25s ease-out;
  background: linear-gradient(90deg, #353535, #717171);
  background: var(--card-background-default);
}

.front {
  display: block;
  z-index: 1;
  color: #ffffff;
  color: var(--card-text-color-default);
  font-family: "Overpass Mono", monospaced;
  font-family: var(--card-text-font);
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.7);
  text-shadow: var(--card-text-shadow);
}

.chip {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  position: absolute;
  top: 62px;
  left: 30px;
  left: var(--card-text-space-left);
  width: 50px;
  height: 40px;
  border: 2px solid #666666;
  border-radius: 8px;
  background: linear-gradient(45deg, rgba(250, 250, 250, 1) 0%, rgba(204, 204, 204, 1) 100%);
}

.chip .horizontalLine {
  position: absolute;
  top: 20px;
  width: 100%;
  border-bottom: 1px solid #aaaaaa;
}

.chip .horizontalLine:before,
.chip .horizontalLine:after {
  content: "";
  position: absolute;
  width: 100%;
  border-bottom: 1px solid #aaaaaa;
  display: block;
}

.chip .horizontalLine:before {
  top: -12px;
}

.chip .horizontalLine:after {
  bottom: -12px;
}

.chip .verticalLine,
.chip .verticalLine:after {
  position: absolute;
  top: 9px;
  left: 15px;
  width: 1px;
  height: 23px;
  background-color: #aaaaaa;
}

.chip .verticalLine:after {
  content: "";
  top: 0;
  left: 17px;
  display: block;
}

.number {
  position: absolute;
  top: 120px;
  left: 30px;
  left: var(--card-text-space-left);
  font-size: 18px;
  letter-spacing: 0.14em;
}

.holderName {
  position: absolute;
  top: 160px;
  left: 30px;
  left: var(--card-text-space-left);
  font-size: 16px;
  letter-spacing: 0.14em;
  text-transform: uppercase;
}

.expiration {
  position: absolute;
  top: 190px;
  left: 30px;
  left: var(--card-text-space-left);
  font-size: 15px;
  justify-content: space-between;
}

.back {
  display: block;
  border-radius: 10px;
  transform: rotateY(180deg) translate3d(0, 0, 1px);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  z-index: 0;
}

.back:before {
  content: "";
  position: absolute;
  top: 10px;
  width: 100%;
  height: 40px;
  background-color: #1b1b1b;
}

.cvv {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  position: absolute;
  top: 66px;
  left: 20px;
  width: 61%;
  padding: 7px 12px;
  background-color: #cccccc;
  font-size: 14px;
  font-family: "Poppins", serif;
  font-style: italic;
  text-align: right;
}

.flipped {
  background: linear-gradient(90deg, #353535, #111111);
  box-shadow: -3px 3px 3px rgba(0, 0, 0, 0.6);
  transform: rotateY(180deg);
}

.flipped .verticalLine,
.flipped .horizontalLine {
  display: none;
}

.flipped .number,
.flipped .holderName,
.flipped .expiration,
.flipped .expiration:before,
.flipped .expiration:after {
  color: #848484;
  text-shadow: 1px -1px 0 #000000, 0 0 1px #ffffff, 1px -1px 0 #000000;
}




.card.caixa-normal-black {
  background: url(/static/media/Card.7fdad895.png);
  background-repeat: no-repeat;
  background-size: 335px 222px;
  border-radius: 16px;
 
 
 

}


.caixa-normal-black .flipped {
