}

.caixa-normal-black .chip:after {
  content: "";
  position: absolute;
  top: 12px;
  left: 125px;
  display: block;
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  font-size: 14px;
}

.caixa-normal-black .holderName {
  top: 190px;
  left: 35px;
}

.caixa-normal-black .number {
  left: 35px;
}

.caixa-normal-black .expiration {
  top: 166px;
  left: 156px;
}

.caixa-normal-black .expiration:before {
  content: "valid thru";
  position: absolute;
  top: -7px;
  left: -40px;
  width: 25px;
  font-size: 0.425rem;
  opacity: 0.8;
  text-transform: uppercase;
}





:root {
  --width-bank-logo: 120px;
}

.bankLogo {
  right: 60%;
  width: auto;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  position: absolute;
  height: 54px;
}




